html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  min-width: 320px;  
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Arial"
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 320px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
}

.header {
	padding: 10px 0;
	margin: 0 auto;
  margin-bottom: 10px;  
  display: block;
}

.header__wrapper {
	position: relative;
}

.header__switcher {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
}

.header__icon {
	background-repeat: no-repeat;
	background-position: center left;
	background-size: contain;  
	display: inline-block;
	height: 20px;
	width: 20px;
}

.header__ava-wrapper,
.header__nickname,
.header__name,
.header__ava,
.header__person {
	min-width: 100px;	
	flex-shrink: 0;
}

.header__info {
	flex-shrink: 1;
}
.project__more {
	position: relative;
	display: block;	
}

.project__backside {
	display: none;	
}

.project__frontside {	
	visibility: visible;	
}

.project__more:hover .project__frontside {
	visibility: hidden;		
}

.project__more:hover .project__backside {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  margin: 0;
  padding: 20px;
  text-decoration: none;
  text-align: center;
  z-index: 5;
  color: grey;
  overflow-y: auto;
  background-image: -webkit-linear-gradient(top, rgba(50, 50, 50, 0.1) 1px, transparent 1px, transparent 100%);
  background-image: linear-gradient(to bottom, rgba(50, 50, 50, 0.1) 1px, transparent 1px, transparent 100%);
  background-color: rgb(244, 244, 244);
}

